
"use client";
import { AuthForm } from "@/components/auth/auth-form";
import { AuthProvider, useAuth } from "@/hooks/use-auth";
import { useRouter } from "next/navigation";
import { useEffect } from "react";
import { ROUTES } from "@/lib/constants";
import { Loader2 } from "lucide-react";

function RegisterPageContent() {
  const { user, isLoading, session } = useAuth();
  const router = useRouter();

  useEffect(() => {
    // If user is already logged in (has a user object and session), redirect them.
    if (!isLoading && user && session) { 
      if (user.role === 'siswa' && !user.isVerified) {
        // If a siswa registers and email verification is pending, they might be redirected
        // here by AuthForm, or if they navigate here while still unverified.
        router.replace(ROUTES.VERIFY_EMAIL);
      } else {
        // Redirect to appropriate dashboard if verified or not a siswa needing verification
        switch (user.role) {
            case 'admin': router.replace(ROUTES.ADMIN_DASHBOARD); break;
            case 'guru': router.replace(ROUTES.GURU_DASHBOARD); break;
            case 'siswa': router.replace(ROUTES.SISWA_DASHBOARD); break; // Verified siswa
            case 'pimpinan': router.replace(ROUTES.PIMPINAN_DASHBOARD); break;
            case 'superadmin': router.replace(ROUTES.ADMIN_DASHBOARD); break;
            default: router.replace(ROUTES.HOME); // Fallback
        }
      }
    }
    // If user is loading, or no user/session, they will see the registration form or loader.
  }, [user, isLoading, session, router]);
  
  // Show loader if checking auth state, or if user is logged in and will be redirected.
  if (isLoading || (!isLoading && user && session)) { 
     return (
      <div className="flex min-h-screen items-center justify-center bg-background">
        <Loader2 className="h-12 w-12 animate-spin text-primary" />
      </div>
    );
  }

  // If not loading and no user/session, show registration form.
  return <AuthForm mode="register" />;
}

export default function RegisterPage() {
  return (
    <AuthProvider>
      <RegisterPageContent />
    </AuthProvider>
  );
}
