
"use client";

import * as React from "react";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import * as z from "zod";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from "@/components/ui/form";
import { useAuth } from "@/hooks/use-auth";
import Link from "next/link";
import { ROUTES, APP_NAME } from "@/lib/constants";
import { Loader2 } from "lucide-react";
import { useRouter } from "next/navigation";
import { useToast } from "@/hooks/use-toast";

const loginSchema = z.object({
  email: z.string().email({ message: "Invalid email address." }),
  password: z.string().min(1, { message: "Password is required." }),
});

const registerSchema = z.object({
  fullName: z.string().min(2, { message: "Full name must be at least 2 characters." }).optional(),
  email: z.string().email({ message: "Invalid email address." }),
  password: z.string().min(6, { message: "Password must be at least 6 characters." }),
});

type AuthFormProps = {
  mode: "login" | "register";
};

export function AuthForm({ mode }: AuthFormProps) {
  const { login, register, isLoading } = useAuth();
  const router = useRouter();
  const { toast } = useToast();
  const [formError, setFormError] = React.useState<string | null>(null);

  const currentSchema = mode === "login" ? loginSchema : registerSchema;

  const form = useForm<z.infer<typeof currentSchema>>({
    resolver: zodResolver(currentSchema),
    defaultValues: {
      email: "",
      password: "",
      ...(mode === "register" && { fullName: "" }),
    },
  });

  async function onSubmit(values: z.infer<typeof currentSchema>) {
    setFormError(null);
    if (mode === "login") {
      const result = await login(values.email, values.password);
      if (result.success && result.user) {
        toast({ title: "Login Successful", description: `Welcome back, ${result.user.name || result.user.email}!`});
        // Redirection is handled by LoginPage useEffect
      } else if (result.error) {
        setFormError(result.error.message || "Invalid credentials.");
        toast({ title: "Login Failed", description: result.error.message || "Invalid credentials.", variant: "destructive" });
      } else {
        setFormError("An unknown error occurred during login.");
        toast({ title: "Login Failed", description: "An unknown error occurred.", variant: "destructive" });
      }
    } else { // mode === "register"
      const registerValues = values as z.infer<typeof registerSchema>;
      const result = await register(registerValues.email, registerValues.password, registerValues.fullName);
      if (result.success && result.user) {
        if (result.requiresConfirmation) {
          toast({ title: "Registration Successful!", description: "Please check your email to verify your account." });
          router.push(ROUTES.VERIFY_EMAIL); 
        } else {
          toast({ title: "Registration Successful!", description: `Welcome to ${APP_NAME}!` });
          // Redirection handled by RegisterPage useEffect
        }
      } else if (result.error) {
        setFormError(result.error.message || "Registration failed.");
        toast({ title: "Registration Failed", description: result.error.message || "Registration failed.", variant: "destructive" });
      } else {
        setFormError("An unknown error occurred during registration.");
        toast({ title: "Registration Failed", description: "An unknown error occurred.", variant: "destructive" });
      }
    }
  }

  return (
    <div className="flex min-h-screen items-center justify-center bg-background p-4">
      <Card className="w-full max-w-md shadow-xl">
        <CardHeader className="text-center">
          <CardTitle className="text-3xl font-headline text-primary">{APP_NAME}</CardTitle>
          <CardDescription className="text-muted-foreground">
            {mode === "login" ? "Sign in to your account" : "Create a new account"}
          </CardDescription>
        </CardHeader>
        <CardContent>
          <Form {...form}>
            <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-6">
              {mode === "register" && (
                <FormField
                  control={form.control}
                  name="fullName"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Full Name</FormLabel>
                      <FormControl>
                        <Input placeholder="John Doe" {...field} />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />
              )}
              <FormField
                control={form.control}
                name="email"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Email</FormLabel>
                    <FormControl>
                      <Input placeholder="you@example.com" {...field} />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
              <FormField
                control={form.control}
                name="password"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Password</FormLabel>
                    <FormControl>
                      <Input type="password" placeholder="••••••••" {...field} />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
              {formError && <p className="text-sm font-medium text-destructive">{formError}</p>}
              <Button type="submit" className="w-full" disabled={isLoading}>
                {isLoading && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
                {mode === "login" ? "Sign In" : "Create Account"}
              </Button>
            </form>
          </Form>
          <div className="mt-6 text-center text-sm">
            {mode === "login" ? (
              <>
                Don&apos;t have an account?{" "}
                <Link href={ROUTES.REGISTER} className="font-medium text-primary hover:underline">
                  Sign up
                </Link>
              </>
            ) : (
              <>
                Already have an account?{" "}
                <Link href={ROUTES.LOGIN} className="font-medium text-primary hover:underline">
                  Sign in
                </Link>
              </>
            )}
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
