
"use client";

import React, { useState, useEffect, createContext, useContext, useCallback } from 'react';
import { useRouter } from 'next/navigation';
import type { User, Role } from '@/types';
import { supabase } from '@/lib/supabaseClient';
import { useToast } from '@/hooks/use-toast';
import { ROUTES, APP_NAME } from '@/lib/constants';
import type { AuthError, Session, User as SupabaseAuthUser } from '@supabase/supabase-js';

interface AuthContextType {
  user: User | null;
  login: (email: string, passwordAttempt: string) => Promise<{ success: boolean; error?: AuthError | null; user?: User | null }>;
  register: (email: string, passwordAttempt: string, fullName?: string) => Promise<{ success: boolean; error?: AuthError | null; requiresConfirmation?: boolean; user?: SupabaseAuthUser | null }>;
  logout: () => Promise<void>;
  isLoading: boolean;
  session: Session | null;
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

export const AuthProvider = ({ children }: { children: React.ReactNode }) => {
  const [user, setUser] = useState<User | null>(null);
  const [session, setSession] = useState<Session | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const router = useRouter();
  const { toast } = useToast();

  const fetchUserProfile = useCallback(async (supabaseUser: SupabaseAuthUser): Promise<User | null> => {
    try {
      const { data: profile, error } = await supabase
        .from('profiles')
        .select('id, name, role, avatar_url')
        .eq('id', supabaseUser.id)
        .single();

      if (error) {
        console.error('Error fetching profile:', error.message);
        // Don't toast here as it might be too frequent during initial load/redirects.
        // Fallback user object if profile fetch fails or doesn't exist (e.g. trigger delay)
        return {
          id: supabaseUser.id,
          email: supabaseUser.email,
          role: 'siswa', // Default role
          isVerified: !!supabaseUser.email_confirmed_at,
          name: supabaseUser.email?.split('@')[0] || 'User',
          avatarUrl: null,
        };
      }

      if (profile) {
        return {
          id: profile.id,
          email: supabaseUser.email,
          name: profile.name,
          role: (profile.role as Role) || 'siswa', // Ensure 'role' in profiles table matches Role type, default to 'siswa'
          avatarUrl: profile.avatar_url,
          isVerified: !!supabaseUser.email_confirmed_at,
        };
      }
      // If profile is null but no error, means it might not exist yet.
      // This can happen if the DB trigger is slow or hasn't run.
      return {
        id: supabaseUser.id,
        email: supabaseUser.email,
        role: 'siswa', // Default role
        isVerified: !!supabaseUser.email_confirmed_at,
        name: supabaseUser.email?.split('@')[0] || 'User',
        avatarUrl: null,
      };
    } catch (e: any) {
      console.error('Exception fetching profile:', e.message);
      return null;
    }
  }, []);

  useEffect(() => {
    setIsLoading(true);
    const { data: authListener } = supabase.auth.onAuthStateChange(async (_event, newSession) => {
      setSession(newSession);
      if (newSession?.user) {
        const profile = await fetchUserProfile(newSession.user);
        setUser(profile);
      } else {
        setUser(null);
      }
      setIsLoading(false);
    });

    // Check initial session
    supabase.auth.getSession().then(async ({ data: { session: initialSession } }) => {
      setSession(initialSession);
      if (initialSession?.user) {
        const profile = await fetchUserProfile(initialSession.user);
        setUser(profile);
      }
      setIsLoading(false);
    });
    
    return () => {
      authListener?.subscription.unsubscribe();
    };
  }, [fetchUserProfile]);

  const login = async (email: string, passwordAttempt: string): Promise<{ success: boolean; error?: AuthError | null; user?: User | null }> => {
    setIsLoading(true);
    const { data, error } = await supabase.auth.signInWithPassword({ email, password: passwordAttempt });
    setIsLoading(false);

    if (error) {
      // toast({ title: "Login Failed", description: error.message, variant: "destructive" });
      return { success: false, error };
    }
    if (data.user) {
       const appUser = await fetchUserProfile(data.user);
       setUser(appUser); // Set user immediately for faster UI update
      if (appUser) {
        // toast({ title: "Login Successful", description: `Welcome back, ${appUser.name || appUser.email}!` });
        return { success: true, user: appUser };
      } else {
        // toast({ title: "Login Succeeded, Profile Error", description: "Could not load full user profile.", variant: "destructive" });
        return { success: true, user: null }; // Still a success in terms of auth
      }
    }
    return { success: false, error: { name: "LoginError", message: "An unknown error occurred during login."} };
  };

  const register = async (email: string, passwordAttempt: string, fullName?: string): Promise<{ success: boolean; error?: AuthError | null; requiresConfirmation?: boolean; user?: SupabaseAuthUser | null }> => {
    setIsLoading(true);
    const { data, error } = await supabase.auth.signUp({
      email,
      password: passwordAttempt,
      options: {
        data: {
          name: fullName, // Will be used by the handle_new_user trigger
          full_name: fullName, // common alternative used in some supabase triggers
        }
      }
    });
    setIsLoading(false);

    if (error) {
      // toast({ title: "Registration Failed", description: error.message, variant: "destructive" });
      return { success: false, error };
    }

    if (data.user) {
      const requiresConfirmation = !data.session; 
      // if (requiresConfirmation) {
        // toast({ title: "Registration Successful!", description: "Please check your email to verify your account." });
      // } else {
        // const appUser = await fetchUserProfile(data.user);
        // setUser(appUser);
        // toast({ title: "Registration Successful!", description: `Welcome to ${APP_NAME}!` });
      // }
      return { success: true, requiresConfirmation, user: data.user };
    }
    
    return { success: false, error: { name: "RegistrationError", message: "An unknown error occurred during registration."} };
  };

  const logout = async () => {
    setIsLoading(true);
    await supabase.auth.signOut();
    setUser(null);
    setSession(null);
    // router.push(ROUTES.LOGIN); // Let pages handle redirect after logout to avoid premature navigation
    toast({ title: "Logged Out", description: "You have been successfully logged out." });
    setIsLoading(false);
  };
  
  const authContextValue: AuthContextType = { 
    user, 
    login, 
    register, 
    logout, 
    isLoading,
    session
  };

  return (
    <AuthContext.Provider value={authContextValue}>
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
};
